.github-button {
    /*fill:#151513; 
    color:#fff; */
    width: 80px; 
    height: 80px; 
    fill: white;
    color: #010F14;
    position: absolute; 
    top: 0; 
    left: 0;
    border: 0; 
    z-index: 10;
    transform:scaleX(-1);
}

.github-corner:hover 
.octo-arm {
    animation:octocat-wave 560ms ease-in-out
}

@keyframes octocat-wave {
    0%, 100% { transform:rotate(0) }
    20%, 60% { transform:rotate(-25deg) }
    40%, 80% { transform:rotate(10deg) }
}

@media (max-width:500px) {
    .github-corner:hover 
    .octo-arm { 
        animation:none 
    }

    .github-corner 
    .octo-arm {
        animation:octocat-wave 560ms ease-in-out
    }

    .github-button {
        width: 60px; 
        height: 60px; 
    }
}
